import { CangjieDataTransfer, Document, Schema, transferUtils, Value, constants } from '@ali/4ever-cangjie';
import logger from '@ali/4ever-logger';
import { escape } from 'lodash-es';
import { mimeTypes as MIME } from '@ali/4ever-utils';
import * as tester from './tester';
import { ClipboardErrorEnum, MARKDOWN_GRAMMER_REGEXP } from './constants';
import filterAndNormalize from './filterAndNormalize';
import { ClipboardPluginConfig, ClipboardFilter, ModelsMap, ReceiveType, Serializer } from './types';

const { MIME_TYPES } = constants;
const cacheMimeTypes = [MIME.APPLICATION_CANGJIE_FRAGMENT, MIME.TEXT_HTML, MIME.TEXT_PLAIN];

/**
 * Ëá™ÂÆö‰πâÂâ™ÂàáÊùøÊï∞ÊçÆÔºåÂ∞ÅË£Ö‰∫Ü fragment Á≠â‰∏Ä‰∫õÁºñËæëÂô®Êï∞ÊçÆ
 */
class ClipboardData implements DataTransfer {
  readonly nativeData: DataTransfer;

  private readonly serializer: Serializer;

  private readonly rules?: ClipboardFilter;

  private readonly schema: Schema;

  private readonly models?: ModelsMap;

  get dropEffect() {
    return this.nativeData.dropEffect;
  }

  get effectAllowed() {
    return this.nativeData.effectAllowed;
  }

  get files() {
    return this.nativeData.files;
  }

  get items() {
    return this.nativeData.items;
  }

  get types() {
    return this.nativeData.types;
  }

  /**
   * 1. ‰ªìÈ¢âÂÜÖÂÆπ‰∏ç‰Ωú markdown ËØ≠Ê≥ïÊ†°È™å
   * 2. Â¶ÇÊûú‰ªÖÊúâ text/plain ÂÜÖÂÆπ Êàñ text ÂÜÖÂÆπÁ¨¶Âêà markdown ËØ≠Ê≥ïÔºåÂàôÂèØËÉΩ‰∏∫ Markdown ÊñáÊú¨
   * 3. Â±û‰∫éÁâπÊÆäÊÉÖÂÜµÔºåÂàôÂèØËÉΩ‰∏∫ Markdown ÊñáÊú¨Ôºö
   *   a. VSCodeÔºöMimeType === vscode-editor-data && mode === 'markdown'
   */
  get maybeMarkdown() {
    if (this.types.includes(MIME.APPLICATION_CANGJIE_FRAGMENT)) {
      return false;
    }
    if (
      this.types.includes(MIME.TEXT_PLAIN) &&
      (this.types.length === 1 ||
        MARKDOWN_GRAMMER_REGEXP.test(
          (this.getData(MIME.TEXT_PLAIN) || '')?.trimStart?.(),
        ))
    ) {
      return true;
    }
    if (this.types.includes(MIME.APPLICATION_VSCODE)) {
      const data = this.getData(MIME.APPLICATION_VSCODE);
      try {
        if (JSON.parse(data).mode === 'markdown') {
          return true;
        }
      } catch {
        return false;
      }
    }
    return false;
  }

  private readonly cangjieData = CangjieDataTransfer.create();

  /**
   * Ââ™ÂàáÊùø‰∏≠ÁöÑ fragment
   */
  private fragment: Document | null = null;

  /**
   * Ââ™ÂàáÊùø‰∏≠Ëß£ÊûêÂá∫ÁöÑ fragment
   */
  private parsedFragment: Document | null = null;

  private cacheId = '';

  /**
   * Á¨¨‰∏ÄÂèØÊé•ÂèóÁ±ªÂûã
   */
  private receiveType?: ReceiveType;

  private readonly onError?: Function;

  constructor(nativeData: DataTransfer, config: ClipboardPluginConfig, models?: ModelsMap) {
    this.nativeData = nativeData;
    const {
      serializer,
      rules,
      schema,
      onError,
    } = config;
    this.serializer = serializer;
    this.rules = rules;
    this.schema = schema;
    this.models = models;
    this.onError = onError;

    this.cacheId = this.getCacheId();
    // ÂΩì‰º†ÂÖ•ÁöÑ nativeData Èùû‰ªìÈ¢âÁ±ªÂûãÊó∂, Êï∞ÊçÆË¶Å copy ‰∏Ä‰ªΩÔºåÈò≤Ê≠¢ÁîüÂëΩÂë®ÊúüÁªìÊùüÂêéË¢´ÈîÄÊØÅ
    if (nativeData && !(nativeData instanceof CangjieDataTransfer)) {
      for (const type of Object.values(MIME_TYPES)) {
        this.cangjieData = this.cangjieData.setData(type, nativeData.getData(type));
      }
    }
  }

  clearData(format?: string | undefined): void {
    this.nativeData.clearData(format);
  }

  getData(format: string): string {
    return this.nativeData.getData(format) || this.cangjieData.getData(format);
  }

  setData(format: string, data: string): void {
    this.nativeData.setData(format, data);
  }

  setDragImage(image: Element, x: number, y: number): void {
    this.nativeData.setDragImage(image, x, y);
  }

  hasData(format: string) {
    return this.types.includes(format);
  }
  
  // event ÊîØÊåÅ mutable ÊñπÂºè‰øÆÊîπÊï∞ÊçÆÔºåÈúÄË¶ÅÈÄöËøá cacheId Ê†°È™å cache fragment ÁöÑÊúâÊïàÊÄß
  getCacheId() {
    for(const type of cacheMimeTypes) {
      const data = this.nativeData.getData(type);
      if (data) {
        return data;
      }
    }
    return '';
  }

  /**
   * ËØªÂèñÂâ™ÂàáÊùø‰∏≠ÁöÑ fragment
   * @param parse ÊòØÂê¶Âº∫Âà∂Ëß£Êûê html/textÔºåÈªòËÆ§‰∏∫ true
   */
  getFragment(parse = true): Document | null {
    // Â¶ÇÊûúÊúâ fragment ÁºìÂ≠òÁõ¥Êé•ËøîÂõû
    const currentCacheId = this.getCacheId();

    if (currentCacheId === this.cacheId) {
      if (this.fragment) {
        return this.fragment;
      }
      // Â¶ÇÊûúÊúâËß£ÊûêÂêéÁöÑ fragment ÁºìÂ≠òÁõ¥Êé•ËøîÂõû
      if (parse && this.parsedFragment) {
        return this.parsedFragment;
      }
    }

    let fragment: Document | null = null;
    try {
      const encoded = this.getData(MIME.APPLICATION_CANGJIE_FRAGMENT);
      if (encoded) {
        // ‰ªé‰ªìÈ¢âÁºñËæëÂô®Êã∑Ë¥ù
        fragment = transferUtils.decodeFragment(encoded);
      }

      if (parse && !fragment) {
        // ÂèçÂ∫èÂàóÂåñËß£ÊûêHtmlÊàñText
        fragment = this.getParseFragment();
      }

      // Â¶ÇÊûúÂâ™ÂàáÊùø‰∏≠Êúâ fragment, ÂØπÂ§çÂà∂Ê∫êÊù•Ëá™‰ªìÈ¢âÊàñÂÖ∂‰ªñÁöÑÂÜÖÂÆπÔºåÁªü‰∏ÄÊî∂Âè£ÂÅönormalizeÂ§ÑÁêÜ
      if (fragment) {
        fragment = this.normalizeFragment(fragment);
      }
    } catch (e: any) {
      if (this.onError) {
        e.type = ClipboardErrorEnum.PASTE_FAILED;
        this.onError(e);
      }
      console.error(e);
      logger.error(e);
    }
    // ‰∏çËß£ÊûêËøîÂõû null
    return fragment;
  }

  /**
   * ËØªÂèñÂâ™ÂàáÊùø‰∏≠ÁöÑ Á∫ØÊñáÊú¨ÂÜÖÂÆπ
   */
  getTextFragment(): Document | null {
    const text = this.getData(MIME.TEXT_PLAIN) || '';
    const { document } = this.serializer.textToValue(text);
    return document;
  }

  /**
   * ËØªÂèñÂâ™Ë¥¥Êùø‰∏≠ÁöÑÁ∫ØÊñáÊú¨ÂÜÖÂÆπÔºåÂπ∂ËøîÂõû‰ª• Markdown Ê†ºÂºèÂ∫èÂàóÂåñÁöÑ Value
   *
   * @returns {Document}
   */
  async getMarkdownFragment(): Promise<Document> {
    const text = this.getData(MIME.TEXT_PLAIN) || '';
    const { document } = await this.serializer.markdownToValue(text);
    return document;
  }

  /**
   * ËØªÂèñÂâ™ÂàáÊùø‰∏≠ÁöÑÂõæÁâáÊñá‰ª∂
   */
  getImages() {
    const data = this.nativeData;
    const { types } = data;
    const exceptFileTypes = (types.includes('Files') && types.filter(type => type !== 'Files')) || [];
    // ms word Êã∑Ë¥ùÁöÑÂõæÊñáÊ∑∑ÂêàÂÜÖÂÆπÔºåmime type ‰ºöÂ∏¶‰∏ä file Á±ªÂûãÔºåËøôÈáåÂà§Êñ≠Èò≤Ê≠¢ËØØÂ§ÑÁêÜ„ÄÇÔºàwps Êó†ËØ•ÈóÆÈ¢òÔºâ
    if ((tester.isMicrosoftOffice(data) && exceptFileTypes.length > 1)) {
      return [];
    }
    // perf: file ËæÉÂ§ßÔºåÊØèÊ¨°ËØªÂèñ data.files ËÄóË¥πÂ§ßÈáèÊó∂Èó¥Ôºå‰ΩøÁî®‰∏¥Êó∂ÂèòÈáèÁºìÂ≠ò‰ºòÂåñ
    const dataFiles = data.files;
    if (!dataFiles) {
      return [];
    }

    const images: File[] = [];
    for (let i = 0, { length } = dataFiles; i < length; i++) {
      const file = dataFiles[i];
      // file Âú®‰∏Ä‰∫õÁâπÊÆäÁöÑÊÉÖÂÜµÊòØ undefined ü§∑‚Äç‚ôÄÔ∏è
      if (file && file.type.startsWith('image/')) {
        images.push(file);
      }
    }
    return images;
  }

  /**
   * Ëé∑ÂèñÁ¨¨‰∏Ä‰∏™ÂèØÊé•ÂèóÁöÑÂâ™ÂàáÊùøÊï∞ÊçÆÁ±ªÂûã
   */
  getFirstReceiveType() {
    if (this.receiveType) {
      return this.receiveType;
    }
    if (this.types.includes('files') && !tester.isMicrosoftOffice(this.nativeData)) {
      this.receiveType = 'files';
    } else if (this.hasData(MIME.APPLICATION_CANGJIE_FRAGMENT)
      || this.hasData(MIME.APPLICATION_SLATE_FRAGMENT)) {
      this.receiveType = 'fragment';
    } else if (this.hasData(MIME.TEXT_HTML)) {
      this.receiveType = 'html';
    } else if (this.hasData(MIME.TEXT_PLAIN)) {
      this.receiveType = 'text';
    } else {
      this.receiveType = 'unknown';
    }
    return this.receiveType;
  }

  // Ê†πÊçÆÂÖ•ÂèÇÂà§Êñ≠ÂÆû‰æãÂåñÂØπË±°ÊòØÂê¶Áõ∏Âêå
  isEqual(
    nativeData: DataTransfer,
    config: ClipboardPluginConfig,
    models?: ModelsMap,
  ) {
    const { serializer, rules, schema } = config;
    return (
      this.nativeData === nativeData &&
      this.serializer === serializer &&
      this.rules === rules &&
      this.schema === schema &&
      this.models === models
    );
  }

  private getTextHtml() {
    let html = '';
    if (this.hasData(MIME.APPLICATION_VSCODE)) {
      html = `<pre>${escape(this.getData(MIME.TEXT_PLAIN))}</pre><p></p>`;
    } else {
      html = this.getData(MIME.TEXT_HTML);
    }
    return html;
  }

  private getParseFragment(): Document | null {
    if (this.parsedFragment) {
      return this.parsedFragment;
    }

    let fragment: Document | null = null;

    if (this.hasData(MIME.TEXT_HTML)) {
      const html = this.getTextHtml();
      if (html) {
        const { document } = this.serializer.htmlToValue(html);
        fragment = document;
        return fragment;
      }
    }

    if (this.hasData(MIME.TEXT_PLAIN)) {
      let text = this.getData(MIME.TEXT_PLAIN);
      if (text) {
        /**
         * ‰ªé Excel ‰∏≠Â§çÂà∂Ë°®Ê†ºÔºåÁ∫ØÊñáÊú¨Á≤òË¥¥‰ºöÈôÑÂ∏¶ \t,ÂΩì \t Ë∂≥Â§üÂ§öÊó∂‰ºöÈÄ†ÊàêÊ∏≤ÊüìÊÄßËÉΩÈóÆÈ¢ò„ÄÇ
         * https://notes.dingtalk.com/doc/E0Vzg7l3wnjKAzJe?orgId=16872003&dd_progress=false&showmenu=false
         * */
        text = text.replace(/\t+/g, '    ');
        const textValue = this.serializer.textToValue(text);
        fragment = textValue.document;
      }
    }
    return fragment;
  }

  normalizeFragment(fragment: Document): Document {
    const newFragment = filterAndNormalize({
      fragment,
      models: this.models,
      rules: this.rules,
      schema: this.schema,
    });
    // Êï∞ÊçÆÊïàÈ™å
    if (this.isValidFragment(newFragment)) {
      this.fragment = newFragment;
      this.parsedFragment = newFragment;
    }
    return newFragment;
  }

  /**
   * Êã∑Ë¥ùËøáÊù•ÁöÑÊï∞ÊçÆ‰∏ç‰∏ÄÂÆöÂÆåÂÖ®ÂèØ‰ø°ÔºåËøôÈáåÂÅö‰∏Ä‰∏ãÊï∞ÊçÆÊïàÈ™å
   * @param fragment
   */
  private isValidFragment(fragment: Document) {
    try {
      this.serializer.valueToJsonML(Value.create({ document: fragment }));
      return true;
    } catch (error) {
      // eslint-disable-next-line no-console
      console.warn(error);
      return false;
    }
  }
}

export default ClipboardData;
